function randomNum(a, b) {
    return Math.floor(Math.random() * b) + a; 
}

class Game {
    constructor() {
        this.gameState = 0;
    }    
}

class Player {
    constructor() {
        this.width = 70;
        this.height = 70;
        this.position = {
            x: playerX,
            y: 0.5*CANVAS_HEIGHT
        }
        this.transform = {
            x: playerX,
            y: 0.5*CANVAS_HEIGHT
        }
        this.gravityDeceleration = 0;
        this.jumpBoost = 0;
        this.stop = 0;
        this.rotation = 0;
        this.direction = 1;
        this.x1 = 0;
        this.y1 = 0;
        this.h1 = 0;
        this.k1 = 0;
        this.fallen = 0;
    }

    draw(ctx) {
        ctx.save();
        
        let upTilt = -50;
        let downTilt = 80;

        this.rotation += this.direction;
        this.direction += 0.5;
        if (this.direction > 1) {
            this.direction = 1;
        }

        if (this.rotation > downTilt) {
            this.rotation = downTilt;
        }
        if (this.rotation < upTilt) {
            this.rotation = upTilt;
        }

        var sin = Math.sin(this.rotation * Math.PI / 180);
        var cos = Math.cos(this.rotation * Math.PI / 180);

        let buffer = 40;
        this.y1 = buffer;
        this.h1 = this.height*Math.cos(this.rotation * Math.PI/180) + this.width*Math.sin(this.rotation * Math.PI/180) - buffer;
        this.x1 = this.height*Math.cos((90 - this.rotation) * Math.PI/180) - buffer;
        this.k1 = this.width*Math.cos(this.rotation * Math.PI/180) + this.height*Math.sin(this.rotation * Math.PI/180) - buffer;
        if (this.rotation < 0) {
            this.y1 = this.width*Math.cos((90 - this.rotation) * Math.PI/180) + buffer;
            this.h1 = this.height*Math.cos(this.rotation * Math.PI/180) - this.width*Math.sin(this.rotation * Math.PI/180) - 1.5*buffer;
            this.x1 = -buffer;
            this.k1 = this.width*Math.cos(this.rotation * Math.PI/180) - this.height*Math.sin(this.rotation * Math.PI/180) - 1.5*buffer;
        }
        this.position.x = this.transform.x - this.x1 - 50;
        this.position.y = this.transform.y + this.y1;
        // ctx.fillRect(this.position.x, this.position.y, this.k1, this.h1); // hit detection

        ctx.transform(cos, sin, -sin, cos, this.transform.x - this.width*cos/2, this.transform.y);

        let imgSrc = document.getElementById("spriteID");
        if (typeof(USER_DETAILS) === "object") {
            let imgID = USER_DETAILS.equipped;
            if (imgID === 2) {
                imgSrc = document.getElementById("draco");
            } else if (imgID === 3) {
                imgSrc = document.getElementById("potter4");
            }
        }
        ctx.drawImage(imgSrc, 0, 0, this.width, this.height);
        ctx.restore();
    }

    fall() {
      this.transform.y += 20;
      if (this.transform.y > CANVAS_HEIGHT) {
        this.fallen = 1;        
      }
    }

    update(deltaTime) {
        this.gravityDeceleration += GRAVITY;
        this.transform.y += this.gravityDeceleration; // toggle gravity
        PLAYER_HEIGHT = this.position.y;
    }

    jump() {
        this.gravityDeceleration = -6;
        this.direction = -7;
        audioJump.play();
    }

}

class Obstacle {
    constructor() {
        this.tops = [0, 10, 20, 30, 40];
        this.speed = 10;
        this.window = 200;//150;
        this.correct = 1;
        this.xRadius = 60;
        this.positionX = CANVAS_WIDTH + OBSTACLE_WIDTH + this.xRadius/2;
        this.passed = false;
        this.a = 0;
        this.b = 0;
        this.question = this.a + " x " + this.b;
    }

    create() {        
        this.a = randomNum(1, 12);
        this.b = randomNum(1, 12);
        this.question = this.a + " x " + this.b;

        this.tops[0] = 0;
        this.tops[1] = randomNum(CANVAS_HEIGHT*0.05, CANVAS_HEIGHT*0.1);
        this.tops[2] = this.tops[1] + this.window;
        this.tops[3] = this.tops[2] + randomNum(CANVAS_HEIGHT*0.1, CANVAS_HEIGHT*0.12);
        this.tops[4] = this.tops[3] + this.window;
        this.correct = 1 + 2*(randomNum(1, 2)-1); // 1 or 3
        this.passed = false;
        
        // this.answerCorrect = ANSWER * randomNum(2, 9); // multiples
        // this.answerIncorrect = ANSWER * randomNum(2, 9) + randomNum(1, ANSWER-1); // multiples
        if (GAME_MODE === "Multiplication") {
            this.answerCorrect = this.a * this.b;
            this.answerIncorrect = this.a * this.b + 1;
        } else {
            this.answerCorrect = 1;
            this.answerIncorrect = 0;
        }
        
    }

    draw(ctx) {
        for (let i = 0; i < this.tops.length; i++) {
            // ctx.fillStyle = "rgb(100, 100, 100)";
            ctx.fillStyle = "#b58b00";
            if (i !== 1 && i !== 3) {
                if (i+1 < this.tops.length) {
                    ctx.fillStyle = "#000";
                    ctx.fillRect(this.positionX-2, this.tops[i]+4, OBSTACLE_WIDTH+4, this.tops[i+1] - this.tops[i]);
                    ctx.fillStyle = "#b58b00";
                    ctx.fillRect(this.positionX, this.tops[i], OBSTACLE_WIDTH, this.tops[i+1] - this.tops[i]);
                } else {
                    ctx.fillStyle = "#000";
                    ctx.fillRect(this.positionX-2, this.tops[i]+4, OBSTACLE_WIDTH+4, CANVAS_HEIGHT - this.tops[i]);
                    ctx.fillStyle = "#b58b00";
                    ctx.fillRect(this.positionX, this.tops[i], OBSTACLE_WIDTH, CANVAS_HEIGHT - this.tops[i]);
                }
            } else {
                ctx.beginPath();
                ctx.lineWidth = 14;
                ctx.strokeStyle = "#000";
                // ctx.ellipse(this.positionX+10, this.tops[i] + (this.tops[i+1] - this.tops[i])/2, 40, (this.tops[i+1] - this.tops[i])/2, 0, 0, 2 * Math.PI); // 3d outline (behind)
                ctx.ellipse(this.positionX, this.tops[i] + (this.tops[i+1] - this.tops[i])/2, this.xRadius, (this.tops[i+1] - this.tops[i])/2, 0, 0, 2 * Math.PI); // outline
                ctx.stroke();
                ctx.beginPath();
                ctx.lineWidth = 10;
                ctx.strokeStyle = "#b58b00";
                ctx.ellipse(this.positionX, this.tops[i] + (this.tops[i+1] - this.tops[i])/2, this.xRadius, (this.tops[i+1] - this.tops[i])/2, 0, 0, 2 * Math.PI);
                ctx.stroke();
                ctx.fillStyle = "rgba(255, 255, 255, 0)";
            }

            // draw scores
            if (i === 1 || i === 3) { 
                ctx.fillStyle = "black";
                ctx.font = "55px harryPotterFont";
                if (i === this.correct) {
                    ctx.fillText(this.answerCorrect, this.positionX - 25, this.tops[i] + this.window/2 + 20);
                } else {
                    ctx.fillText(this.answerIncorrect, this.positionX - 25, this.tops[i] + this.window/2 + 20);
                }
            }

            if (i === 2) {
                ctx.fillStyle = "black";
                ctx.fillText(this.question, this.positionX - 150, (this.tops[i] + this.tops[i+1])/2);
            }
        }
    }

    update(deltaTime) {        
        this.positionX -= SPEED;

        if (this.positionX + OBSTACLE_WIDTH + this.xRadius < 0) {
            this.positionX = CANVAS_WIDTH + OBSTACLE_WIDTH + this.xRadius/2;
            this.create();
        }
        SCORE += SPEED;
    }
}

class ObstacleRight {
    constructor() {
        this.top1 = 0;
        this.top2 = 0;
        this.speed = 0;
        this.window = 0;//150;
        this.xRadius = 0;
        this.positionX = 0;
    }

    draw(ctx) {
        ctx.beginPath();
        ctx.lineWidth = 14;
        ctx.strokeStyle = "#000";
        // ctx.ellipse(this.positionX+10, this.tops[i] + (this.tops[i+1] - this.tops[i])/2, 40, (this.tops[i+1] - this.tops[i])/2, 0, 0, 2 * Math.PI); // 3d outline (behind)
        // ctx.ellipse(this.positionX, this.top1 + (this.top1 - this.top1)/2, this.xRadius, (this.tops[i+1] - this.tops[i])/2, 0, 0, 2 * Math.PI); // outline
        ctx.ellipse(this.positionX, this.top1 + this.window/2, this.xRadius, this.window/2, 0, -Math.PI/2, Math.PI/2); // outline
        ctx.stroke();

        ctx.beginPath();
        ctx.lineWidth = 10;
        ctx.strokeStyle = "#b58b00";
        ctx.ellipse(this.positionX, this.top1 + this.window/2, this.xRadius, this.window/2, 0, -Math.PI/2, Math.PI/2);
        ctx.stroke();

        ctx.beginPath();
        ctx.lineWidth = 14;
        ctx.strokeStyle = "#000";
        ctx.ellipse(this.positionX, this.top2 + this.window/2, this.xRadius, this.window/2, 0, -Math.PI/2, Math.PI/2);
        ctx.stroke();

        ctx.beginPath();
        ctx.lineWidth = 10;
        ctx.strokeStyle = "#b58b00";
        ctx.ellipse(this.positionX, this.top2 + this.window/2, this.xRadius, this.window/2, 0, -Math.PI/2, Math.PI/2);
        ctx.stroke();

        ctx.fillStyle = "rgba(255, 255, 255, 0)";
    }

    update(deltaTime, obstacles) {
        this.positionX = obstacles.positionX;
        this.top1 = obstacles.tops[1];
        this.top2 = obstacles.tops[3];
        this.positionX = obstacles.positionX;
        SCORE += SPEED;
    }
}

class Bludger {
  constructor() {
    this.radius = 30;
    this.position = {
      x: CANVAS_WIDTH + this.radius,
      y: CANVAS_HEIGHT/2
    }   
    this.bludger_hit = 0;
  }
  
  draw(ctx) {
    ctx.fillStyle = "rgb(0, 0, 0)";
    ctx.strokeStyle = "rgb(0, 0, 0)";
    if (this.position.x > playerX) {
      ctx.beginPath();
      ctx.arc(this.position.x, this.position.y, this.radius, 0, 2 * Math.PI);
      ctx.fill();
      ctx.stroke();
      // ctx.fillRect(this.position.x, this.position.y, 10, 10);
    } else {
      this.bludger_hit = 1;
      // let audio = new Audio("./snd/roblox.mp3");
      console.log("got em");
    }
  }

  update(deltaTime) {
    if (SHOW_RESULTS) {
      let bludgerSpeed = 50;
      this.position.x -= bludgerSpeed;
      let w = (CANVAS_WIDTH - this.position.x)/(CANVAS_WIDTH - playerX);
      // this.position.y = (this.position.y - PLAYER_HEIGHT) * (1 - playerX)/(CANVAS_WIDTH - playerX);      
      this.position.y = CANVAS_HEIGHT/2 - w*(CANVAS_HEIGHT/2 - PLAYER_HEIGHT)
    }    
  }
  
}

