class Cloud {
    constructor() {
        this.position = {
            x: randomNum(100, CANVAS_WIDTH),
            y: randomNum(10, CANVAS_HEIGHT/2)
        }
        this.size = randomNum(20, 40);
        this.cloudSpeed = randomNum(1, 10)/10;
    }

    draw(ctx) {
        let cloudSrc = document.getElementById("cloudID");
        ctx.drawImage(cloudSrc, this.position.x, this.position.y, this.size, this.size);
    }

    update(deltaTime) {
        this.position.x -= this.cloudSpeed;
        if (this.position.x < -this.size) {
            this.position.x = CANVAS_WIDTH;
            this.size = randomNum(20, 40);
            this.cloudSpeed = randomNum(1, 10)/10;
            this.position.y = randomNum(10, CANVAS_HEIGHT/2);
        }
    }
}

class Coin {
    constructor() {
        this.position = {
            x: CANVAS_WIDTH + 50,
            y: 50
        }
        this.size = 40;
        this.totalFrames = 4;
        this.currentFrame = 0;
        this.active = 0;
    }

    draw(ctx) {
        if (this.active === 1) {
            let frame = Math.floor(this.currentFrame) % this.totalFrames;
            let coinSrc = document.getElementById("coinID_" + frame);
            ctx.drawImage(coinSrc, this.position.x, this.position.y, this.size, this.size);
        }        
    }

    update(deltaTime) {
        if (this.active === 1) {
            let gifSpeed = 0.2;
            this.currentFrame += gifSpeed;
            let coinSpeed = 2;
            this.position.x -= coinSpeed;
            this.position.y = CANVAS_HEIGHT/2 - ((CANVAS_HEIGHT - 50 - 50)/2) * Math.sin(CANVAS_WIDTH - this.position.x/200);
            if (this.position.x + this.size < 0) {
                // reset
                this.active = 0;
            }
        } else {
            this.position.x = CANVAS_WIDTH + 50;
        }

    }
}

const Flappy = () => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const [loaded, setLoaded] = useState(0);
    
    useEffect(() => {
        if (loaded === 0) {
            console.log("loaded: " + loaded);
            let savedUsername = sessionStorage.getItem("savedUsername");
            if (savedUsername) {
                let savedPassword = sessionStorage.getItem("savedPassword");
                
                // showUserDetails(savedUsername, savedPassword);
                startApp(savedUsername, savedPassword);
            } else {
                document.getElementsByClassName("signIn")[0].style.display = "none";
                document.getElementsByClassName("signUp")[0].style.display = "block";
            }
            recordUser();
        }
        
    });

    async function recordUser() {
        setLoaded(1);
        // local storage (id)
        let id = parseInt(localStorage.getItem("id"));
        if (!id) {
            let records = await firebase.getRecordIds();
            id = Math.max(...records) + 1;
            localStorage.setItem("id", id);
        }
        // store id and date

        let epochtime = Date.now();
        let d = new Date(epochtime);
        let utc = d.getTime() + (d.getTimezoneOffset() * 60000);  //This converts to UTC 00:00
        let offset = 10;
        let nd = new Date(utc + (3600000*offset));
        let tz = nd.toLocaleString();
        await firebase.addRecord(tz, id);
    }

    async function startApp(uname, pass) {
        await loginUser(uname, pass)
    }

    async function loginUser(uname, pass) {
        USER_DETAILS = await firebase.loginUser(uname, pass);
        if (typeof(USER_DETAILS) === "object") {
            HIGH_SCORE = USER_DETAILS.highscore;//await firebase.getHighScore(username);
            console.log("username is " + USER_DETAILS.username);
            console.log("HIGH_SCORE is " + HIGH_SCORE);

            sessionStorage.setItem("savedUsername", uname);
            sessionStorage.setItem("savedPassword", pass);
            setUsername(uname);
            setPassword(pass);
            toggleForm();
            showUserDetails(USER_DETAILS);
            showCharacters();
            // TOP_HIGH_SCORES = await firebase.getTopHighScores();
        } else {
            setPassword("");
            alert(USER_DETAILS);
        }
    }

    async function showUserDetails(USER_DETAILS) {
        document.getElementsByClassName("signIn")[0].style.display = "block";
        document.getElementsByClassName("signUp")[0].style.display = "none";
        document.getElementById("usernameID").innerHTML = "Welcome " + USER_DETAILS.username;   
        // let points = await firebase.getPoints(USER_DETAILS.);
        document.getElementById("pointsID").innerHTML = "You have " + USER_DETAILS.points + " points";
        document.getElementById("pointsID").innerHTML += "<br>High Score: " + HIGH_SCORE + " points";
    }
    
    function collisionCheck(game, player, obstacles, bludger, ctx) {
        if (player.transform.y > CANVAS_HEIGHT) {
            player.transform.y = CANVAS_HEIGHT;
            game.gameState = -1;
            deathSound.play();
        }

        let passed = true;
        if (player.position.x + player.k1 > obstacles.positionX
            && player.position.x < obstacles.positionX + OBSTACLE_WIDTH) {
                passed = false;
                if (player.position.y > obstacles.tops[obstacles.correct]
                    && player.position.y + player.h1 < obstacles.tops[obstacles.correct] + obstacles.window) {
                        passed = true;
                        audioSuccess.play();
                }
        }

        if (passed === false) {
            game.gameState = -1;
            SHOW_RESULTS = true;
            // document.getElementById("testID").innerHTML = "HIT";
            obstacles.draw(ctx);
            player.draw(ctx);
            deathSound.play();
        }
    }

    async function updatePoints() {
        let savedUsername = sessionStorage.getItem("savedUsername");
        if (savedUsername) {
            await firebase.updatePoints(savedUsername, SCORE);
            USER_DETAILS.points += SCORE;
        }
    }

    function weatherCheck() {
        // let weather = getWeather();
        let weather = 1;
        let backgroundImg = "sunnyBackground";
        if (weather === "rain") {
            backgroundImg = "rainBackground";
        } else if (weather === "overcast") {
            backgroundImg = "overcastBackground";
        }
        return backgroundImg;
    }
    
    
    async function startGame() {
        console.log("game has begun, speed is " + SPEED);
        
        if (backgroundMusic.paused) {
            backgroundMusic.play();
        } else {
            backgroundMusic.play();
        }

        weatherCheck();
        
        document.getElementById("gameScreen").width = CANVAS_WIDTH;
        document.getElementById("gameScreen").height = CANVAS_HEIGHT;
        document.getElementById("speedID").innerHTML = "Speed: " + SPEED;
        // let canvas = document.getElementById("gameScreen");
        // let ctx = canvas.getContext("2d");
        
        let game = new Game();
        game.gameState = 1;

        let btns = document.getElementsByClassName("menuBtns");
        for (let i = 0; i < btns.length; i++) {
            btns[i].disabled = true;
        }
        document.getElementById("musicSwitchID").style.background = "grey";
        document.getElementById("sfxSwitchID").style.background = "grey";

        let player = new Player();
        // let player = new TestPlayer();

        let snitch = new Snitch();

        document.addEventListener("keydown", event => {
            switch (event.key) {
                case " ":
                    event.preventDefault();
                    //player.jumpBoost = 1;
                    player.jump();
                    break;
                default:
            }
        });

        let canvas = document.getElementById("gameScreen");
        canvas.addEventListener("click", function() { player.jump() });
        let ctx = canvas.getContext("2d");
        loadFonts(ctx);
        ctx.font = "60px harryPotterFont";
        
        player.draw(ctx);

        // countdown delay
        if (COUNTDOWN) {
            for (let i = 4; i > 0; i--) {
                ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
                /// font doesnt load straight away
                if (i === 4) {
                    ctx.fillStyle = "#fff";
                    ctx.fillText("Starting...", -CANVAS_WIDTH, 0);
                    await new Promise(resolve => {
                        setTimeout(() => {
                        resolve(2);
                        }, 100);
                    });
                } else {
                    // ctx.font = "60px Verdana";
                    ctx.fillStyle = "rgb(100, 100, 100)";
                    // ctx.fillText("Multiples of " + ANSWER, CANVAS_WIDTH*0.3, CANVAS_HEIGHT/2);
                    ctx.fillText(i, CANVAS_WIDTH*0.5, CANVAS_HEIGHT/2);
                    // delay countdown
                    await new Promise(resolve => {
                        setTimeout(() => {
                        resolve(2);
                        }, 1000);
                    });
                }
            }
        }
        
        
        let obstacles = new Obstacle();
        obstacles.create();
        let obstacleRight = new ObstacleRight();
        obstacleRight.top1 = obstacles.tops[1];
        obstacleRight.top2 = obstacles.tops[3];
        obstacleRight.speed = obstacles.speed;
        obstacleRight.window = obstacles.window;
        obstacleRight.xRadius = obstacles.xRadius;
        obstacleRight.positionX = obstacles.positionX;

        let bludger = new Bludger();

        let clouds = [];
        let coin = new Coin();

        requestAnimationFrame(
            function(timestamp) {
                gameLoop(timestamp, ctx, game, player, obstacles, bludger, snitch, clouds, coin, obstacleRight);
            }
        );
    }

    function makeQuestion() {
        ANSWER = randomNum(2, 10);
    }


    function updateBackground(ctx) {
        let backgroundSkySrc = document.getElementById("backgroundSkyID");
        let backgroundTowersSrc = document.getElementById("backgroundTowersID");

        let q1 = document.getElementById("quid01");
        let q2 = document.getElementById("quid02");
        let q3 = document.getElementById("quid03");
        let q4 = document.getElementById("quid04");
        let imgHeight = CANVAS_HEIGHT/3.5;
        let ratio = imgHeight / backgroundTowersSrc.height;        
        backgroundTowersSrc.height *= ratio;
        backgroundTowersSrc.width *= ratio;

        ratio = imgHeight / q1.height;
        q1.height *= ratio;
        q1.width *= ratio;
        q2.height *= ratio;
        q2.width *= ratio;
        q3.height *= ratio;
        q3.width *= ratio;
        q4.height *= ratio;
        q4.width *= ratio;

        let slide = SCORE/15;

        ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
        ctx.drawImage(backgroundSkySrc, 0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
        // let i = 0;
        // while ((i-1)*backgroundTowersSrc.width < CANVAS_WIDTH) {
        //     let xpos = -slide%backgroundTowersSrc.width + i*backgroundTowersSrc.width;
        //     ctx.drawImage(backgroundTowersSrc, xpos, CANVAS_HEIGHT-backgroundTowersSrc.height, backgroundTowersSrc.width, backgroundTowersSrc.height);
        //     i++;
        // }

        let imgSrc = [q1, q2, q3, q4];
        let numTiles = Math.ceil(CANVAS_WIDTH / (imgSrc.length * imgSrc[0].width)) + 1;
        for (let tile = 0; tile < numTiles; tile++) {
            for (let img = 0; img < imgSrc.length; img++) {
                ctx.drawImage(imgSrc[img], imgSrc[img].width*img - slide%(imgSrc[img].width * imgSrc.length) + tile * imgSrc[img].width * imgSrc.length, CANVAS_HEIGHT-imgSrc[img].height, imgSrc[img].width, imgSrc[img].height);
            }
        }
    }

    function updateClouds(ctx, clouds, deltatime) {
        if (clouds.length < 5) {
            let cloud = new Cloud();
            clouds.push(cloud);
        }
        for (let c = 0; c < clouds.length; c++) {
            clouds[c].update(deltatime);
            clouds[c].draw(ctx);
        }
    }

    let lastTime = 0;
    async function gameLoop(timestamp, ctx, game, player, obstacles, bludger, snitch, clouds, coin, obstacleRight) {
        let deltatime = timestamp - lastTime;
        lastTime = timestamp;

        updateBackground(ctx);
        updateClouds(ctx, clouds, deltatime);

        if (coin.active === 0 && randomNum(1, 1000) === 1) {
            console.log("coin has been activated");
            coin.active = 1;
            coinMusic.play();
        }

        // check coin collision
        if (player.position.x + player.width > coin.position.x) {
            if (player.position.x < coin.position.x + coin.size) {
                if (player.position.y + player.height > coin.position.y) {
                    if (player.position.y < coin.position.y + coin.size) {
                        coin.active = 0;
                        coinMusic.pause();
                        coinCollect.play();
                        COIN_BONUS++;
                    }
                }
            }
        }

        coin.draw(ctx);
        coin.update(deltatime);
        
        // question text
        ctx.font = "60px harryPotterFont";
        ctx.fillStyle = "rgb(100, 100, 100)";
        if (GAME_MODE === "Multiples") {
            ctx.fillText("Multiples of " + ANSWER, CANVAS_WIDTH*0.3, CANVAS_HEIGHT/2);
        }
        ctx.font = "40px harryPotterFont";
        ctx.fillText("Level " + LEVEL, 10, 50);
        
        if (game.gameState === 1) {
            player.update(deltatime);
            if (player.position.x > obstacles.positionX && obstacles.passed === false) {
                LEVEL++;
                MULTIPLIER += 0.1;
                obstacles.passed = true;
            }
            collisionCheck(game, player, obstacles, bludger, ctx);
        }
        obstacles.update(deltatime);
        obstacles.draw(ctx);
        player.draw(ctx);
        obstacleRight.update(deltatime, obstacles);
        obstacleRight.draw(ctx);
        
        snitch.update(deltatime);
        snitch.draw(ctx);
        
        if (game.gameState !== 1 && bludger.bludger_hit === 0) {
            bludger.update(deltatime);
            bludger.draw(ctx);
        }
        if (game.gameState !== 1 && bludger.bludger_hit === 1 && player.fallen === 0) {
            player.fall();
            player.draw(ctx);
        }

        if (player.position.y < CANVAS_HEIGHT) {
            if (game.gameState !== -1 || bludger.bludger_hit === 0 || player.fallen === 0) {
                requestAnimationFrame(
                    function(timestamp) {
                        gameLoop(timestamp, ctx, game, player, obstacles, bludger, snitch, clouds, coin, obstacleRight);
                    }
                );
            }
        } else {
            console.log("game over");    
            coinMusic.pause();
            
            // calculate points
            if (LEVEL > 1) {
                if (MULTIPLIER > 10) {
                    MULTIPLIER = 10;
                }
                SCORE = parseInt((LEVEL * (MULTIPLIER + 1) * 100).toFixed(0)) + COIN_BONUS*500;
            } else {
                SCORE = 0;
            }
            
            ctx.fillStyle = "rgb(250, 250, 250, 0.8)";
            ctx.fillRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
            document.getElementById("resetBtn").style.display = "block";

            // ctx.font = "40px goodTimesFont";
            // ctx.fillStyle = "black";
            // ctx.fillText("Level:                  " + (LEVEL).toFixed(0) + " x 100", 500, 60);
            // ctx.fillText("Streak multiplier:        x" + (MULTIPLIER + 1).toFixed(1), 500, 135);
            // ctx.fillText("Coin bonus:             + 500 x " + COIN_BONUS.toFixed(0), 500, 200);
            // ctx.font = "60px goodTimesFont";
            // ctx.fillText("Total score:     " + SCORE.toFixed(0), 460, 300);
            document.getElementsByClassName("showScores")[0].style.display = "block";
            document.getElementById("scoresBreakdown").innerHTML = `
                <table>
                    <tbody>
                        <tr><td>Level:</td><td>` + (LEVEL).toFixed(0) + ` x 100</td></tr>
                        <tr><td>Streak multiplier:</td><td>x` + (MULTIPLIER + 1).toFixed(1) + `</td></tr>
                        <tr><td>Coin bonus:</td><td>+500 x ` + COIN_BONUS.toFixed(0) + `</td></tr>
                    </tbody>
                </table>
            `;
            document.getElementById("totalScore").innerHTML = "Total Score: " + SCORE.toFixed(0);

            await updatePoints();
            TOP_HIGH_SCORES = await firebase.getTopHighScores();

            if (SCORE > HIGH_SCORE) {
                HIGH_SCORE = SCORE;
                await updateHighScore();
            }

            showHighScores();

            let btns = document.getElementsByClassName("menuBtns");
            for (let i = 0; i < btns.length; i++) {
                btns[i].disabled = false;
            }
            if (document.getElementById("musicSwitchID").checked) {
                document.getElementById("musicSwitchID").style.background = "#34c759";
            } else {
                document.getElementById("musicSwitchID").style.background = "#c7c7cc";
            }
            if (document.getElementById("sfxSwitchID").checked) {
                document.getElementById("sfxSwitchID").style.background = "#34c759";
            } else {
                document.getElementById("sfxSwitchID").style.background = "#c7c7cc";
            }
        }

        if (WORK_MODE) {
            ctx.filter = 'grayscale(1)';
            document.getElementById("musicSwitchID").disabled = true;
            document.getElementById("sfxSwitchID").disabled = true;
            SPEED = 10;
        }

    }

    function loadFonts(ctx) {
        ctx.font = "1px harryPotterFont";
        ctx.fillText("a", 0, CANVAS_HEIGHT + 10);
        ctx.font = "1px bladeRunnerFont";
        ctx.fillText("a", 0, CANVAS_HEIGHT + 10);
        ctx.font = "1px goodTimesFont";
        ctx.fillText("a", 0, CANVAS_HEIGHT + 10);
    }

    async function updateHighScore() {
        let savedUsername = sessionStorage.getItem("savedUsername");
        if (savedUsername) {
            alert("Congrats! You've beaten your high score!");
            await firebase.updateHighScore(username, HIGH_SCORE);
            let new_high_score_list = [];
            console.log(TOP_HIGH_SCORES);
            if (TOP_HIGH_SCORES.length > 0) {
                let madeList = false;
                for (let h = 0; h < TOP_HIGH_SCORES.length; h++) {
                    if (madeList === false) {
                        if (HIGH_SCORE > TOP_HIGH_SCORES[h].highscore) {
                            let p = {
                                username: savedUsername,
                                highscore: HIGH_SCORE
                            }
                            new_high_score_list.push(p);
                            madeList = true;
                        }
                    }
                    if (new_high_score_list.length < TOP_HIGH_SCORES.length) {
                        if (TOP_HIGH_SCORES[h].username !== savedUsername) {
                            new_high_score_list.push(TOP_HIGH_SCORES[h]);
                        }
                    }
                }
                if (madeList === true) {
                    // alert("Congrats! You made the all time list");                        
                    await firebase.updateHighScoresList(new_high_score_list);
                    TOP_HIGH_SCORES = new_high_score_list;
                }
            }
        }
    }

    function showHighScores() {
        console.log("high scores:");
        document.getElementById("highScoresList").innerHTML = "<table><tbody>";
        let t = "";
        for(let i = 0; i < TOP_HIGH_SCORES.length; i++) {
            console.log(TOP_HIGH_SCORES[i].username + ":" + TOP_HIGH_SCORES[i].highscore);
            if (i === 0) {
                t = "<table><tbody>";
            }
            t += `<tr><td>` + TOP_HIGH_SCORES[i].username + `</td><td>` + TOP_HIGH_SCORES[i].highscore + `</td></tr>`;
            if (i === TOP_HIGH_SCORES.length - 1) {
                t += "</tbody></table>";
            }
        }
        document.getElementById("highScoresList").innerHTML = t;
    }

    async function reset() {
        let savedUsername = sessionStorage.getItem("savedUsername");
        let points = await firebase.getPoints(savedUsername);
        document.getElementById("pointsID").innerHTML = "You have " + points + " points";
        document.getElementById("pointsID").innerHTML += "<br>High Score: " + HIGH_SCORE + " points";
        SCORE = 0;
        LEVEL = 1;
        MULTIPLIER = 0;
        COIN_BONUS = 0;
        console.log("reset");
        document.getElementById("resetBtn").style.display = "none";
        SHOW_RESULTS = false;
        document.getElementsByClassName("showScores")[0].style.display = "none";

        makeQuestion();
        // setTimeout(startGame, 1500);
        startGame();
    }

    function decreaseSpeed() {
        SPEED--;
        if (SPEED < 0) {
            SPEED = 0;
        }
        document.getElementById("speedID").innerHTML = "Speed: " + SPEED;
    }

    function increaseSpeed() {
        SPEED++;
        if (SPEED > 10) {
            SPEED = 10;
        }
        document.getElementById("speedID").innerHTML = "Speed: " + SPEED;
    }

    function btnMsg(msg) {
        document.getElementById("formMsgID").innerHTML = msg;
        document.getElementById("formSubmitMsg").innerHTML = msg;
        toggleForm();
    }
    
    function toggleForm() {
        if (document.getElementById("loginFormID").style.display === "block") {
            document.getElementById("loginFormID").style.display = "none";
            document.getElementById("openLoginBtnID").style.display = "block";
            document.getElementById("openSignUpBtnID").style.display = "block";
        } else {
            document.getElementById("loginFormID").style.display = "block";
            document.getElementById("openLoginBtnID").style.display = "none";
            document.getElementById("openSignUpBtnID").style.display = "none";
        }
    }

    async function signUser(event) {
        event.preventDefault();
        // check if loggin in or signing up
        if (username.length >= 4 && username.length <= 50) {
            if (password.length >= 4 && password.length <= 50) {
                if (document.getElementById("formMsgID").innerHTML === "Login") {
                    // await firebase.loginUser()
                    console.log("logging in user");
                    loginUser(username, password);
                } else {
                    // sign up check
                    console.log("creating user");
                    let result = await firebase.signUpUser(username, password);
                    if (result === false) {
                        alert("A username with that email already exists. Please use a different email");
                    } else {
                        USER_DETAILS = loginUser(username, password);
                    }
                }
                console.log("'" + username + "'");
                console.log("'" + password + "'");
            } else {
                alert("Password must be between 4 and 50 characters long");
                setPassword("");
            }
        } else {
            alert("Username must be between 4 and 50 characters long");
        }
    }

    function logout() {
        sessionStorage.clear();
        setUsername("");
        setPassword("");
        USER_DETAILS = false;
        showCharacters();
        document.getElementsByClassName("signIn")[0].style.display = "none";
        document.getElementsByClassName("signUp")[0].style.display = "block";
    }

    function showCharacters() {
        let savedUsername = sessionStorage.getItem("savedUsername");
        
        let charactersEquip = document.getElementsByClassName("characterBtnEquipSet");
        let charactersPurch = document.getElementsByClassName("characterBtnPurch");

        for (let i = 0; i < charactersEquip.length; i++) {
            if (savedUsername) {
                if (USER_DETAILS.items % parseInt(charactersEquip[i].value) === 0) {
                    charactersEquip[i].style.display = "block";
                    charactersPurch[i].style.display = "none";
                    charactersEquip[i].innerHTML = "Equip character";
                    if (USER_DETAILS.equipped === parseInt(charactersEquip[i].value)) {
                        charactersEquip[i].innerHTML = "Character equipped";
                    }
                }               
            } else {
                if (i > 0) {
                    charactersEquip[i].style.display = "none";
                    charactersPurch[i].style.display = "block";                    
                }                    
            }  
        }
    }

    async function equipCharacter(c) {
        let savedUsername = sessionStorage.getItem("savedUsername");
        if (savedUsername) {
            let prime = 1;
            if (c === "draco") {
                prime = 2;
            } else if (c === "potter4") {
                prime = 3;
            }
            await firebase.updateCharacter(savedUsername, prime);
            USER_DETAILS.equipped = prime;
            showCharacters();
        }
    }

    function toggleMusic() {
        let musicSwitch = document.getElementById("musicSwitchID");
        if (musicSwitch.checked) {
            backgroundMusic.muted = false;
            musicSwitch.style.background = "#34c759";
        } else {
            backgroundMusic.muted = true;
            musicSwitch.style.background = "#c7c7cc";
        }
    }

    function toggleSFX() {
        let sfxSwitch = document.getElementById("sfxSwitchID");
        if (sfxSwitch.checked) {
            deathSound.muted = false;
            audioSuccess.muted = false;
            audioJump.muted = false;
            coinMusic.muted = false;
            sfxSwitch.style.background = "#34c759";
        } else {
            deathSound.muted = true;
            audioSuccess.muted = true;
            audioJump.muted = true;
            coinMusic.muted = true;
            sfxSwitch.style.background = "#c7c7cc";
        }
    }

    async function purchaseCharacter(c) {
        let savedUsername = sessionStorage.getItem("savedUsername");
        if (savedUsername) {
            console.log(c);
            let prime = 1;
            let points = 0;
            if (c === "draco") {
                prime = 2;
                points = 5000;
            } else if (c === "potter4") {
                prime = 3;
                points = 7500;
            } else {
                console.log("ERR: character is " + c);
            }
            if (prime > 1 && points > 0) {
                if (points <= USER_DETAILS.points) {
                    await firebase.purchaseCharacter(USER_DETAILS.username, prime, points);
                    alert("Character " + c + " was successfully purchased");
                } else {
                    alert("You do not have enough points to purchase this character");
                }
            }
        } else {
            alert("you need to have an account and be signed in to purchase other characters");
        }
        
    }

    return ( 
        <div className="Flappy">
            <br />
            <div className="signUp">
                <button id="openLoginBtnID" type="submit" className="btn btn-primary" onClick={() => btnMsg("Login")}>Login</button>
                <button id="openSignUpBtnID" type="submit" className="btn btn-success" onClick={() => btnMsg("Create User")}>Create Account</button>
                <form id="loginFormID" style={{display:"none"}} onSubmit={signUser}>
                    <h4 id="formMsgID">Login</h4>
                    <div className="form-group">
                        <label>Username</label>
                        <input type="text" required value={username} onChange={(e) => setUsername(e.target.value)} className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Username" />
                    </div>
                    <div className="form-group">
                        <label>Password</label>
                        <input type="password" required value={password} onChange={(e) => setPassword(e.target.value)} className="form-control" id="exampleInputPassword1" placeholder="Password" />
                    </div>
                    <button id="formSubmitMsg" type="submit" className="btn btn-primary">Login</button>
                    <button className="btn btn-secondary" onClick={toggleForm}>Hide</button>
                </form>
            </div>
            
            <br />
            <div id="gameScreenDiv">
                <canvas id="gameScreen"  height={CANVAS_HEIGHT} width={CANVAS_WIDTH}></canvas>
                <div className="showScores" style={{display:"none"}}>
                    <div id="userScores">
                        <h3>Your Scores</h3>
                        <br />
                        <div id="scoresBreakdown"></div>
                        <br />
                        <div id="totalScore"></div>
                    </div>
                    <div id="highScores">
                        <h3>High Scores</h3>
                        <br />
                        <div id="highScoresList"></div>
                    </div>
                </div>
            </div>
            <br />

            <table>
                <tbody>
                    <tr>
                        <td>
                            <div className="signIn" style={{display:"none"}}>
                                <h4 id="usernameID"> </h4>
                                <p id="pointsID"> </p>
                                <button className="btn btn-secondary" onClick={logout}>Logout</button>
                            </div>
                        </td>
                        <td>
                            <button id="resetBtn" className="btn btn-light menuBtns" onClick={reset}>Play Again?</button>
                            <p id="testID"></p>
                            <button className="speedBtn btn btn-light menuBtns" onClick={decreaseSpeed} style={{display: "inline"}}>-</button>
                            <p id="speedID" style={{display: "inline"}}>Speed:</p>
                            <button className="speedBtn btn btn-light menuBtns" onClick={increaseSpeed} style={{display: "inline"}}>+</button>
                        </td>
                        <td>
                            <label>Music</label>
                            <input id="musicSwitchID" onChange={toggleMusic} className="ios-switch --light menuBtns" type="checkbox" />
                            <br />
                            <label>SFX</label>
                            <input id="sfxSwitchID" onChange={toggleSFX} className="ios-switch --light menuBtns" type="checkbox" />
                        </td>
                    </tr>
                </tbody>
            </table>

            <img id="spriteID" alt="potter" src={potter} style={{display:"none"}}></img>
            <img id="coinID_0" alt="coin" src={coin_0} style={{display:"none"}}></img>
            <img id="coinID_1" alt="coin" src={coin_1} style={{display:"none"}}></img>
            <img id="coinID_2" alt="coin" src={coin_2} style={{display:"none"}}></img>
            <img id="coinID_3" alt="coin" src={coin_3} style={{display:"none"}}></img>
            <img id="backgroundSkyID" alt="background" src={backgroundSky} style={{display:"none"}} ></img>
            <img id="quid01" alt="background" src={q01} style={{display:"none"}} ></img>
            <img id="quid02" alt="background" src={q02} style={{display:"none"}} ></img>
            <img id="quid03" alt="background" src={q03} style={{display:"none"}} ></img>
            <img id="quid04" alt="background" src={q04} style={{display:"none"}} ></img>
            <img id="backgroundTowersID" alt="background" src={backgroundTowers} style={{display:"none"}} ></img>
            <img id="cloudID" alt="background" src={cloud} style={{display:"none"}} ></img>

            <br /><br /><br />
            {/* unlockable characters */}
            <h4>Characters</h4>
            <br />
            <div className="store">
                <div className="character">
                    <h5>Harry</h5>
                    <img id="potter" alt="background" src={potter}></img>
                    <button className="characterBtnEquipSet" onClick={() => equipCharacter("potter")} value="1" style={{display:"block"}}>Equip character</button>
                    <button className="characterBtnPurch" onClick={() => purchaseCharacter("potter")} value="1" style={{display:"none"}}>Purchase for 0 points</button>
                </div>
                <div className="character">
                    <h5>Draco</h5>
                    <img id="draco" alt="background" src={draco}></img>
                    <button className="characterBtnEquipSet" onClick={() => equipCharacter("draco")} value="2" style={{display:"none"}}>Equip character</button>
                    <button className="characterBtnPurch" onClick={() => purchaseCharacter("draco")} value="2">Purchase for 5000 points</button>
                </div>
                <div className="character">
                    <h5>Harry - Quidditch</h5>
                    <img id="potter4" alt="background" src={potter4}></img>
                    <button className="characterBtnEquipSet" onClick={() => equipCharacter("potter4")} value="3" style={{display:"none"}}>Equip character</button>
                    <button className="characterBtnPurch" onClick={() => purchaseCharacter("potter4")} value="3">Purchase for 7500 points</button>
                </div>
            </div>
            

        </div>
    );
}

export default Flappy;