import firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/auth';
import 'firebase/database';
import app from 'firebase/app';

const firebaseConfig = {
    apiKey: "AIzaSyAHN7qi76sqh-ZHSlAGryJEbTvxcsvFJaY",
    authDomain: "user-database-test-9ee1d.firebaseapp.com",
    databaseURL: "https://user-database-test-9ee1d.firebaseio.com",
    projectId: "user-database-test-9ee1d",
    storageBucket: "user-database-test-9ee1d.appspot.com",
    messagingSenderId: "425239506606",
    appId: "1:425239506606:web:decf3cc0780d2d8e9d073f"
};

class Firebase {
    
    constructor() {
        app.initializeApp(firebaseConfig);
        this.auth = app.auth();
        this.db = app.firestore;
        this.u_id = 0;
        this.currentUserID = 0;
        this.currentUsername = "";        
    }

    async checkUsers(username) {
        console.log("checking if user is in database...");
        let passed = true;
        let db = "jumpGame-users";
        let x = await firebase.database().ref(db).once('value');
        for (let k in x.val()) {
            if (x.val()[k].username === username) {
                console.log("we already have that user");
                passed = false;
            }
        }
        return passed;
    }

    async getPoints(username) {
        console.log("getting points...");
        let db = "jumpGame-users";
        let x = await firebase.database().ref(db).once('value');
        for (let k in x.val()) {
            if (x.val()[k].username === username) {
                return x.val()[k].points;
            }
        }
        return 0;
    }

    async getHighScore(username) {
        console.log("getting high score...");
        let db = "jumpGame-users";
        let x = await firebase.database().ref(db).once('value');
        let highscore = 0;
        for (let k in x.val()) {
            if (x.val()[k].username === username) {
                highscore = x.val()[k].highscore;
            }
        }
        console.log("high score returned");
        return highscore;
    }

    async updateHighScore(username, highScore) {
        console.log("updating highscore...");
        let db = "jumpGame-users";
        let x = await firebase.database().ref(db).once('value');
        let key = 0;
        for (let k in x.val()) {
            if (x.val()[k].username) {
                if (username === x.val()[k].username) {
                    let points = parseInt(highScore);
                    let objKey = Object.keys(x.val())[key];
                    await firebase.database().ref(db + "/" + objKey).child("highscore").set(points);
                } else {
                    key++;
                }
            }
        }
        console.log("highscore updated");
    }

    async getTopHighScores() {
        console.log("getting top high scores...");
        let db = "jumpGame-highscores";
        let x = await firebase.database().ref(db).once('value');
        let h = [];
        for (let k in x.val()) {
            let hs = {
                username: x.val()[k].username,
                highscore: x.val()[k].highscore,
                rank: x.val()[k].rank
            }
            h.push(hs);
        }
        console.log("top high scores returned");
        return h;
    }

    async updateHighScoresList(new_list) {
        console.log("updating high scores lists...");

        let db = "jumpGame-highscores";
        let x = await firebase.database().ref(db).once('value');
        let key = 0;
        for (let k in x.val()) {
            let obj = new_list[x.val()[k].rank - 1];
            let objKey = Object.keys(x.val())[key];
            await firebase.database().ref(db + "/" + objKey).child("username").set(obj.username);
            await firebase.database().ref(db + "/" + objKey).child("highscore").set(obj.highscore);
            key++;
        }
        console.log("list updated");
    }

    async signUpUser(username, password) {
        console.log("signing up user...");
        // check user is not already in database
        let check = await this.checkUsers(username);
        let result = false;
        if (check === true) {
            let db = "jumpGame-users";
            await firebase.database().ref(db).push({
                username : username,
                password : password,
                points : 0,
                highscore: 0,
                items : 1

            });
            result = true;
            console.log("adding user to the database...");
        }
        console.log("user is signed up");
        return result;
    }

    async loginUser(username, password) {
        console.log("logging in user...");
        let db = "jumpGame-users";
        let x = await firebase.database().ref(db).once('value');
        let result = false;
        for (let k in x.val()) {
            if (x.val()[k].username) {
                if (username === x.val()[k].username) {
                    if (password === x.val()[k].password) {
                        result = {
                            username: x.val()[k].username,
                            equipped: x.val()[k].equipped,
                            items: x.val()[k].items,
                            points: x.val()[k].points,
                            highscore: x.val()[k].highscore
                        };
                        console.log("user successfully logged in");
                        return result;
                    } else {
                        result = "Incorrect password";
                        return result;
                    }
                }
            }
        }
        result = "Account for username " + username + " does not exist. Please create an account.";
        return result;
    }

    async purchaseCharacter(username, prime, points) {
        console.log("purchasing character...");
        let db = "jumpGame-users";
        let x = await firebase.database().ref(db).once('value');
        let key = 0;
        let dataKey = key;
        let userspoints = 0;
        let items = 1;
        for (let k in x.val()) {
            if (x.val()[k].username) {
                if (x.val()[k].username === username) {
                    if (x.val()[k].items % prime !== 0) {
                        userspoints = parseInt(x.val()[k].points);
                        items = parseInt(x.val()[k].items);
                        dataKey = key;
                        let objKey = Object.keys(x.val())[dataKey];
                        await firebase.database().ref(db + "/" + objKey).child("points").set(userspoints - points);
                        await firebase.database().ref(db + "/" + objKey).child("items").set(items * prime);
                    }                    
                } else {
                    key++;
                }
            }
        }
        console.log("character purchased");
    }

    async updateCharacter(username, item) {
        console.log("updating character...");
        let db = "jumpGame-users";
        let x = await firebase.database().ref(db).once('value');
        let key = 0;
        let dataKey = key;
        for (let k in x.val()) {
            if (x.val()[k].username) {
                if (x.val()[k].username === username) {
                    dataKey = key;
                    let objKey = Object.keys(x.val())[dataKey];
                    await firebase.database().ref(db + "/" + objKey).child("equipped").set(item);
                } else {
                    key++;
                }
            }
        }
        console.log("character updated");
    }

    async getRecordIds() {
        console.log("getting ids...");
        let db = "jumpGame-records";
        let x = await firebase.database().ref(db).once('value');
        let records = [0];
        for (let k in x.val()) {
            records.push(x.val()[k].id);
        }
        console.log("ids returned");
        return records;
    }

    async addRecord(tz, id) {
        console.log("adding record" + tz + ", " + id + "...");
        let db = "jumpGame-records";
        await firebase.database().ref(db).push({
            timestamp: tz,
            id : id
        });
        console.log("record added");
    }

    async updatePoints(username, p) {
        console.log("updating points...");
        let db = "jumpGame-users";
        let x = await firebase.database().ref(db).once('value');
        let key = 0;
        let dataKey = key;
        for (let k in x.val()) {
            if (x.val()[k].username) {
                if (username === x.val()[k].username) {
                    let points = parseInt(x.val()[k].points);
                    p += points;
                    dataKey = key;
                } else {
                    key++;
                }
            }
        }
        let objKey = Object.keys(x.val())[dataKey];
        await firebase.database().ref(db + "/" + objKey).child("points").set(p);
        console.log("points updated");
        return p;
    }

}

export default new Firebase();
